//
// Styles
// ============================================================================

.block-wrapper-header,
.block-wrapper-content,
.block-wrapper-cta {
  position: relative;
  z-index: auto !important; // stylelint-disable-line declaration-no-important
}

.block-background {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -2;
  overflow: hidden;
}

.block-wrapper-header {
  .block-background {
    @media (max-width: 1920px) {
      background: url(../images/parallax/header_1920.png) fixed no-repeat 50% 66.97% / cover;
    }

    @media (min-width: 1921px) {
      background: url(../images/parallax/header_2560.png) fixed no-repeat 50% 66.97% / cover;
    }

    @media (min-width: 2560px) {
      background: url(../images/parallax/header_3840.png) fixed no-repeat 50% 66.97% / cover;
    }

    // stylelint-disable media-feature-name-no-vendor-prefix
    @media only screen and (-webkit-min-device-pixel-ratio: 1.5) and (min-width: 1200px),
      only screen and (min-resolution: 144dpi) and (min-width: 1200px) {
      background: url(../images/parallax/header_2560.png) fixed no-repeat 50% 66.97% / cover;
    }

    @media only screen and (-webkit-min-device-pixel-ratio: 1.5) and (min-width: 1920px),
      only screen and (min-resolution: 144dpi) and (min-width: 1920px) {
      background: url(../images/parallax/header_3840.png) fixed no-repeat 50% 66.97% / cover;
    }

    @media only screen and (-webkit-min-device-pixel-ratio: 1.5) and (min-width: 2560px),
      only screen and (min-resolution: 144dpi) and (min-width: 2560px) {
      background: url(../images/parallax/header_5120.png) fixed no-repeat 50% 66.97% / cover;
    }
    // stylelint-enable
  }

  .jumbotron {
    padding: 8rem 0;
    margin-bottom: 0;
    background-color: rgba(22, 68, 168, .7);
  }
}

.block-wrapper-content {
  .block-background {
    background-color: #fff;
  }

  section {
    padding: 4rem 0;
  }
}

.block-wrapper-cta {
  .block-background {
    background: url(../images/background/call_to_action.png) center center / 1232px repeat, rgb(30, 97, 249);
  }

  section {
    padding: 8rem 0;
  }
}
